import { useState, useEffect } from 'react';

const useEmployees = () => {
  // URL de votre serveur local JSON Server
  const apiUrl = 'http://localhost:3001/employees';

  // tat pour stocker les donnes des employs
  const [employees, setEmployees] = useState([]);
  // tat pour indiquer s'il y a une erreur lors du chargement des employs
  const [error, setError] = useState(null);
  // tat pour indiquer si les donnes sont en cours de chargement
  const [isLoading, setIsLoading] = useState(false);

  // Fonction pour rcuprer les donnes des employs
  const fetchEmployees = async () => {
    setIsLoading(true);
    try {
      // Effectuer une requte GET vers l'API
      const response = await fetch(apiUrl);

      // Vrifier si la rponse est ok (status code 200)
      if (!response.ok) {
        throw new Error('Failed to fetch employees');
      }

      // Analyser la rponse JSON
      const data = await response.json();
      
      // Mettre  jour l'tat des employs avec les donnes rcupres
      setEmployees(data);
      setIsLoading(false);
    } catch (error) {
      // Mettre  jour l'tat d'erreur en cas d'chec de la requte
      setError(error);
      setIsLoading(false);
    }
  };

  // Utiliser useEffect pour charger les employs lors du montage du composant
  useEffect(() => {
    fetchEmployees();
  }, []); // Dpendance vide pour excuter uniquement une fois lors du montage

  // Renvoyer les donnes des employs, l'tat de chargement et l'erreur
  return { employees, isLoading, error };
};

export default useEmployees;
