const postEmployee = async (newEmployeeData) => {
    try {
      // URL de votre serveur local JSON Server
      const apiUrl = 'http://localhost:3001/employees';
  
      // Effectuer une requte POST vers l'API avec les nouvelles donnes d'employ
      const response = await fetch(apiUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newEmployeeData),
      });
  
      // Vrifier si la rponse est ok (status code 201 pour une cration russie)
      if (!response.ok) {
        throw new Error('Failed to add employee');
      }
  
      // Rcuprer la rponse JSON
      const data = await response.json();
  
      // Afficher la rponse dans la console
      console.log('Employee added successfully:', data);
  
      return data;
    } catch (error) {
      console.error('Error adding employee:', error.message);
      throw error;
    }
  };
  
  export default postEmployee;
  